.button{
    background-color: #4d1601;
    color: white;
    padding: 0.5rem 1rem;
    cursor: pointer;
    font: inherit;
    border: none;
    padding: 0.5rem 2rem;
    display: flex;
    justify-content: space-around;
    align-items: center;
    border-radius: 25px;
    font-weight: bold;
}

.button:hover,
.button:active {
  background-color: #2c0d00;
}

.icon{
    background-color: #4d1601;
    margin-right: 0.5rem;
    width: 1.35rem;
    height: 1.35rem;
}

.badge{
    margin-left: 1rem;
    background-color:  #b94517;
    padding: 0.25rem 1rem;
    border-radius: 25px;
    font-weight: bold;
}

.button:hover .badge,
.button:active .badge {
  background-color: #92320c;
}

.bump {
  animation: bump 300ms ease-out;
}

@keyframes bump {
  0% {
    transform: scale(1);
  }
  10% {
    transform: scale(0.9);
  }
  30% {
    transform: scale(1.1);
  }
  50% {
    transform: scale(1.15);
  }
  100% {
    transform: scale(1);
  }
}

@media (max-width:500px){
  .button{
    padding: 0.5rem 1rem;
  }
}